import React from "react";
import { FaSearch } from 'react-icons/fa';

import { Container, Row, Col } from "reusecore/Layout";
import SectionTitle from "reusecore/SectionTitle";
import Button from "reusecore/Button";
import Particle from "reusecore/Particle";
import Link from 'gatsby-link';
import { FaPlay, FaDownload } from "react-icons/fa";
import { FaFacebookF, FaTwitter, FaLinkedinIn, FaTelegram, FaGithub, FaMedium, FaInstagram } from "react-icons/fa";
import BannerSectionWrapper from "./banner.style";
import imgHero1 from "assets/images/hosting/banner/01.png";
import imgHero2 from "assets/images/hosting/banner/02.png";

const BannerHosting = () => {
  return (
    <BannerSectionWrapper>
      <img src={imgHero1} alt="img" className="section-particle one" />
      <Particle />
      <Container fullWidthSM>
        <Row Vcenter={true}>
          <Col lg={6}>
            <SectionTitle
              className="section-title"
              UniWidth="100%"
            >
              {/* <h4>Save up to <span>70%</span> with our holiday Deals</h4> */}
              <h1>
                Knowlege, Skills, Tools and Experinces
                Generate Profits
              </h1>
              <p>
                join with our trading community and <br />
                start your trading journey...
              </p>
            </SectionTitle>
            <div className="domain-search-block">
              {/* <div className="search-box">
                <input type='text' placeholder='Search domain' />
                <Button className="src-btn">
                  <FaSearch />
                </Button>
              </div> */}
              <div className="domain-extention-block">
                <span className="com">

                    <Link to="https://t.me/tradinginsightnet" target="_blank">
                      <a> {"Telegram      "}
                        <FaTelegram />
                      </a>
                    </Link>

                </span>
                {/* <span className="com">
                    <Link to="https://t.me/tradinginsightnet" target="_blank">
                      <a> {"Telegram      "}
                        <FaInstagram/>
                      </a>
                    </Link>
                </span> */}
              </div>
            </div>
          </Col>
          <Col lg={6}>
            <div className="hero-img-block">
              <img src={imgHero2} alt="img" />
            </div>
          </Col>
        </Row>
      </Container>
    </BannerSectionWrapper>
  );
};

export default BannerHosting;
