import React from "react";
import Link from 'gatsby-link'
import { Container } from "reusecore/Layout";
import SectionTitle from "reusecore/SectionTitle";
import Button from "reusecore/Button";
import ContactWrapper from "./contact.style";
import VintageBox from "reusecore/VintageBox";
import { FaFacebookF, FaTwitter, FaLinkedinIn, FaTelegram, FaGithub, FaMedium, FaInstagram, FaYoutube } from "react-icons/fa";

const Contact = () => {
  return (
    <ContactWrapper id="contact">
      <SectionTitle className="section-title" UniWidth="55%">
        <h4>Contact</h4>
        <h2>
          <span>Follow us</span>.
        </h2>
      </SectionTitle>
      <SectionTitle className="section-title" UniWidth="55%">
          <Button className="banner-btn one"><Link href=" http://t.me/pandemiumToken">
            <a>
              <FaTelegram />
            </a>
          </Link></Button>
          <Button className="banner-btn youtube"><Link href=" https://twitter.com/PandemiumToken">
            <a>
              <FaYoutube />
            </a>
          </Link></Button>
          <Button className="banner-btn two"><Link href=" https://twitter.com/PandemiumToken">
            <a>
              <FaInstagram />
            </a>
          </Link></Button>
          <Button className="banner-btn three"><Link href="https://github.com/Pandemium">
            <a>
              <FaTwitter />
            </a>
          </Link></Button>
          {/* <Button className="banner-btn four"><Link href="https://github.com/Pandemium">
            <a>
              <FaGithub />
            </a>
          </Link></Button> */}
      </SectionTitle>
    </ContactWrapper>
  );
};

export default Contact;
